import { FC } from 'react';
import ModalNPM from 'react-modal';

export type ModalProps = {
  isOpen: boolean;
  closeModal?: () => {};
  label?: string
}

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    width: '400px',
    borderRadius: 16
  },
};

export const Modal: FC<ModalProps> = ({
  isOpen,
  closeModal,
  label
}) => {
  return (
    <ModalNPM
      style={customStyles}
      isOpen={isOpen}
      onRequestClose={closeModal}
      contentLabel={label || 'asdasd'}
      appElement={document.body}
    >
      <div>
        <h1 style={{textAlign: 'center', marginBottom: 28}}>
          {label || 'asdasd'}
        </h1>
        <button style={{fontSize: 16,border: 0, borderRadius: 4, background: 'coral', float: 'right', padding: '12px 18px'}} onClick={()=>{closeModal && closeModal()}}>
          submit
        </button>
      </div>
    </ModalNPM>
  );
};

export default Modal
